body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background: rgb(245, 245, 245);
  color: rgba(0, 0, 0);
  font-family: 'Droid Serif', serif;
  position: relative;
  font-size: 18px;
  max-width: 100vw;
  min-width: 300px;
  min-height: 100vh;
  transition: background 0.4s ease-out, color 0.4s ease-out;
  -webkit-transition: background 0.4s ease-out, color 0.4s ease-out;
}


* {
  box-sizing: border-box;
}

button {
  outline:0;
  border: none;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  opacity: 0.8;
  margin: 0 0 10px;
  font-weight: 400;
  font-style: normal;
}

.app.dark {
  background: #1b1b1d;
  color: rgba(240, 240, 240, 0.9);
}

.app.dark .box {
  background: #5a5a5a9d;
}

.app.dark a:hover > .link {
  color: #00e2bc;
}

a:hover > .link {
  color: #22785e;
}

.minimize {
  height: 0;
  width: 0;
  color: transparent;
  z-index: -1;
}

.app-wrapper {
  background: inherit;
  position: relative;
  padding-bottom: 36px;
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
}

.app-wrapper.full-height {
  height: 100%;
  display: inline;
}

/***//***//***//* ARTICLE MODAL*//***//***//***//***/

.article-container {
  text-align: right;
}

.article-author {
  opacity: 0.8;
  padding-top: 8px;
}

.article-date {
  font-size: 0.8em;
  opacity: 0.8;
  padding-bottom: 10px;
}

.article-description {
  font-size: 0.9em;
  width: 90%;
  text-align: left;
  font-style: italic;
}

.article-content {
  text-align: left;
  opacity: 0.9;
  font-size: 1.3em;
}
/***//***//***//***//***//***//***//***//***//***/

.title {
  font-family: "Georgia, Times", Times New Roman, sans-serif;
  font-weight: 700;
  font-size: 56px;
  display: inline-block;
  line-height: 65px;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.app-header {
  text-align: center;
  background: inherit;
  z-index: 10;
  font-size: 14px;
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
  height: 40px;
  border-bottom: 1px solid rgba(212, 218, 223, 0.6);
  box-shadow: 0 3px 6px 0 rgba(116, 129, 141, 0.1);
  -webkit-box-shadow: 0 3px 6px 0 rgba(116, 129, 141, 0.1);
  border-top: 1.2px solid;
  padding: 10px 0;
}

.app-body {
  justify-content: center;
  display: flex;
  background: inherit;
  min-height: 100vh;
  width: 100%;
}

.modal-wrapper {
  position: relative;
  background: inherit;
  width: 100%;
  height: inherit;
  text-align: center;
  z-index: 2;
  display: flex;
  justify-content: center;
}

.content-wrapper {
  display: flex;
  position: relative;
  justify-content: center;
  padding-top: 10px;
}


.articles {
  max-width: 650px;
  padding: 0 15px;
  border-right: 1px solid #79797957;
  width: 50vw;
}

.sidebar {
  max-width: 500px;
  width: 40vw;
  padding: 0 16px;
  height: auto;
}

.link {
  text-decoration: underline;
}

.app-footer {
  width: 100%;
  background: inherit;
  color: inherit;
  font-size: 14px;
  padding: 30px;
  height: 40px;
  opacity: 0.6;
  border-top: 1px solid #79797957;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 3;
  box-sizing: border-box;
}

.note a {
  font-size: 12px;
  font-style: italic;
}

.note-link {
  padding: 0 5px;
}

@media only screen and (max-width:568px) {
  .app {
    font-size: 16px;
  }

  .app-wrapper {
    padding: 0;
  }

  .title {
    font-size: 36px;
    line-height: 50px;
    padding: 0 10px;
  }

  .app-header {
    height: 42px;
    font-size: 12px;
  }

  .app-footer {
    height: 80px;
    flex-direction: column;
    justify-content: center;
  }

  .content-wrapper {
    flex-direction: column;
    padding: 0 15px;
    width: 100%;
  }

  .articles {
    padding: 15px 0;
    border-right: none;
    border-bottom: 4px double #79797957;
    width: 100%;
  }

  .sidebar {
    width: 100%;
    padding: 16px 0;
  }
}



.header {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.time {
    padding: 0 3px;
    margin-left: 3px;
    border-left: 1px solid;
    opacity: 0.7;
    font-size: 0.9em;
}

.dark-mode-toggle {
    display: grid;
    grid-template-columns: 36px 1fr;
    align-items: baseline;
}



.switch {
  display: block;
  position: relative;
 	width: 22px;
  height: 10px;
  border-radius: 20px;
  background: rgb(61, 110, 104);
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  border-collapse: separate;
  transition: background 0.4s ease-in-out;
  -webkit-transition: background 0.4s ease-in-out;
}

.switch.off {
  background: rgb(163, 163, 163);
}

#switch {
 	clip: rect(0 0 0 0);
  position: absolute;
  visibility: hidden;
}

.slider {
  box-sizing: border-box;
  height: 16px;
  width: 16px;
  background: #fff;
  box-shadow: 0 0 1px 1px rgb(163, 163, 163);
  border-radius: 50%;
  position: absolute;
  top: -3px;
  left: 14px;
  border-collapse: separate;
  transition: all 0.4s ease-in-out;
}

.slider.off {
  left: -6px;
} 
.responsive-image {
  width: 100%;
  height: auto;
  max-height: 1000px;
  max-width: 1000px;
}

.caption a {
  font-size: 0.7em;
  color: inherit;
  opacity: 0.6;
  text-decoration: none;
  text-align: right;
}

.floating {
  box-shadow: 0 3px 6px 0 rgba(116, 129, 141, 0.1), 0 3px 10px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0 3px 6px 0 rgba(116, 129, 141, 0.1), 0 3px 10px rgba(0, 0, 0, 0.3);
}
.social-media-box {
  height: 90%;
}

.box {
  position: sticky;
  position: -webkit-sticky;
  top: 50px;
  height: 200px;
  width: 100%;
  margin: 5px 0;
  background: #d3d3d3;
  border: 1px solid #79797957;
  padding: 15px;
}

.box .contact-title, .box .links-title {
  margin: 0;
  padding: 8px 0;
}

.box div a {
  opacity: 0.6;
  font-size: 15px;
}

@media only screen and (max-width:568px) {
  .social-media-box {
    padding: 15px 0;
    width: 100%;
    height: auto;
  }

  .box {
    width: 100%;
    position: relative;
    top: 0;
  }
}
.article-comp {
  margin-bottom: 12px;
  padding: 8px 0px;
  border-bottom: 1px dotted #64646480
}

.hide {
  display: none;
}

.hoverable {
  cursor: pointer;
  transition: text 0.3s ease-in-out;
}

.article-title {
  padding-top: 8px;
  line-height: 1em;
  color: inherit;
  font-size: 0.9em;
}

.article-link,
.article-source {
  font-size: 0.8em;
  color: rgba(61, 61, 61, 0.8);
}

.article-source {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 80%;
}

.app.dark .article-source{
  color: rgba(255, 255, 255, 0.7);
}

.app.dark .hoverable:hover > .article-title  {
  text-decoration: underline;
  text-decoration-color: #00e2bc;
  -webkit-text-decoration-color: #00e2bc;
}

.hoverable:hover > .article-title  {
  text-decoration: underline;
  text-decoration-color: #22785e;
  -webkit-text-decoration-color: #22785e;
}
.news-comp {
  width: 100%;
}
.loader-comp {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}
.wineglass {
  height: 36px;
  position: relative;
}
.wineglass:after {
  content: ''; 
  position: absolute;
  top: 47px;
  left: 5px;
  width: 20px;
  height: 5px;
  background: white;
  box-shadow: 0 0 1px white;
}
.top {
  background: white;
  width: 30px;
  height: 36px;
  border-radius: 0 0 36px 36px;
  box-shadow: 0 0 1px white;
}
.top:before {
  content: ''; 
  position: absolute;
  left: 4px;
  bottom: 4px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: linear-gradient(transparent 50%, #EF810E 50%);
}
.app.dark .top:before {
  background: linear-gradient(transparent 50%, rgba(86, 65, 126, 0.5) 50%);
}
.left .top:before {
  -webkit-animation: rotate2 2s linear infinite;
          animation: rotate2 2s linear infinite;
}
.right .top:before {
  -webkit-animation: rotate1 2s linear infinite;
          animation: rotate1 2s linear infinite;
}
.top:after {
  content: ''; 
  position: absolute;
  top: 35px;
  left: 12px;
  width: 6px;
  height: 13px;
  background: white;
  box-shadow: 0 0 1px white;
}
.left {
  display: inline-block;
  margin-right: 10px;
  -webkit-animation: rotate1 2s cubic-bezier(.39,1.52,.46,.92) infinite;
          animation: rotate1 2s cubic-bezier(.39,1.52,.46,.92) infinite;
}
.right {
  display: inline-block;
  -webkit-animation: rotate2 2s cubic-bezier(.39,1.52,.46,.92) infinite;
          animation: rotate2 2s cubic-bezier(.39,1.52,.46,.92) infinite;
}
@-webkit-keyframes rotate1{
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform:rotate(22deg);transform:rotate(22deg);}
}
@keyframes rotate1{
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
  50% {-webkit-transform:rotate(22deg);transform:rotate(22deg);}
}
@-webkit-keyframes rotate2{
  0% {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  50% {-webkit-transform:rotate(-22deg);transform:rotate(-22deg);}
}
@keyframes rotate2{
  0% {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  50% {-webkit-transform:rotate(-22deg);transform:rotate(-22deg);}
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 70px;
  height: 25px;
  position: relative;
  opacity: 0;
  -webkit-animation: 2s fadeInUp infinite linear 2s;
          animation: 2s fadeInUp infinite linear 2s;
}
li {
  width: 6px;
  height: 15px;
  background: white;
  position: absolute;
  box-shadow: 0 0 1px white;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
}
li:nth-child(1) {
  left: 26px;
  bottom: 5px;
  -webkit-transform: rotate(-35deg);
          transform: rotate(-35deg);
}
li:nth-child(2) {
  left: 34px;
  bottom: 8px;
}
li:nth-child(3) {
  left: 42px;
  bottom: 5px;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
}
@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  
  33% {
    opacity: 1;
    -webkit-transform: scaleY(1.4);
            transform: scaleY(1.4);
  }
  64% {
     opacity: .1;
     -webkit-transform: scaleY(1);
             transform: scaleY(1);
  }
  100% { 
    opacity: 0;
    -webkit-transform: scaleY(.3);
            transform: scaleY(.3);
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  
  33% {
    opacity: 1;
    -webkit-transform: scaleY(1.4);
            transform: scaleY(1.4);
  }
  64% {
     opacity: .1;
     -webkit-transform: scaleY(1);
             transform: scaleY(1);
  }
  100% { 
    opacity: 0;
    -webkit-transform: scaleY(.3);
            transform: scaleY(.3);
  }
}
.modal-comp {
  height: 100%;
  position: relative;
  max-width: 1000px;
}

.modal-close-button-container {
  position: absolute;
  left: 5px;
  top: 25px;
  height: 100%;
}

.modal-close-icon {
  width: 26px;
  height: 26px;
  top: 50px;
  left: 0;
  z-index: 3;
  background: gray;
  color: white;
  border-radius: 50%;
  position: -webkit-sticky;
  position: sticky;
  transition: box-shadow 0.3s ease-in out;
}

.modal-close-icon:hover {
  box-shadow: 0 3px 6px 0 rgba(116, 129, 141, 0.3);
}

.modal-content {
  height: 100%;
  margin: 0 18px;
  padding: 26px;
  box-shadow: 0 3px 6px 0 rgba(116, 129, 141, 0.1);
  border: 1px solid rgba(212, 218, 223, 0.6);
  border-top: none;
  border-bottom: none;
  box-sizing: border-box;
}

@media only screen and (max-width:568px) {
  .modal-content {
    margin: 0 16px;
    padding: 12px;
    border-bottom: none;
    box-shadow: none;
  }
  
  .modal-close-icon {
    width: 30px;
    height: 30px;
  }
  .modal-close-button-container {
    left: 2px;
    top: 24px;
  }
}
